html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 200;
  src: local("Montserrat-ExtraLight"), local("Montserrat-ExtraLight"), url("../fonts/Montserrat-ExtraLight.woff2") format("woff2"), url("../fonts/Montserrat-ExtraLight.woff") format("woff");
}

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 300;
  src: local("Montserrat-Light"), local("Montserrat-Light"), url("../fonts/Montserrat-Light.woff2") format("woff2"), url("../fonts/Montserrat-Light.woff") format("woff");
}

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: local("Montserrat-Regular"), local("Montserrat-Regular"), url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
}

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: local("Montserrat-Medium"), local("Montserrat-Medium"), url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
}

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: local("Montserrat-SemiBold"), local("Montserrat-SemiBold"), url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
}

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 800;
  src: local("Montserrat-ExtraBold"), local("Montserrat-ExtraBold"), url("../fonts/Montserrat-ExtraBold.woff2") format("woff2"), url("../fonts/Montserrat-ExtraBold.woff") format("woff");
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: currentColor;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-size: 16px;
  font-family: "Montserrat", "Helvetica", sans-serif;
  font-weight: 600;
  line-height: 1.4;
  color: #fff;
  background-color: #00bdca;
  min-width: 320px;
  overflow-x: hidden;
}

a {
  text-decoration: none;
  color: #fff;
}

a:hover {
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

p {
  margin: 0;
}

ul, li {
  padding: 0;
  margin: 0;
  list-style: none;
}

.out {
  overflow: hidden;
  position: relative;
}

.container {
  width: 100%;
  max-width: 1230px;
  margin: 0 auto;
  padding: 0 30px;
}

.block {
  display: flex;
}

.bg-rotated {
  width: 300%;
  height: 32%;
  transform: rotate(30deg);
  background: #1d354e;
  position: absolute;
  left: -120%;
  top: 13.2%;
  z-index: -3;
}

.header__container {
  display: flex;
  padding: 60px 0 0;
  max-width: 1060px;
}

.header__logo {
  display: flex;
  align-items: center;
  margin-left: 55px;
}

.header__logo img {
  width: 44px;
  height: 44px;
  margin-right: 12px;
}

.header__logo .title {
  line-height: 1;
  font-size: 13px;
  font-weight: 500;
}

.header__logo .title h1 {
  font-size: 24px;
  font-weight: 600;
}

.header__logo .title h1 span {
  color: #56ecf6;
}

.header__menu {
  margin-left: auto;
}

.nav {
  display: flex;
  align-items: center;
  line-height: 3.3;
}

.navigation__item {
  margin-right: 34px;
}

.header__login {
  margin-left: 5px;
  margin-right: 8px;
}

.header__login .login_log {
  margin-right: 30px;
}

.header__login .login_sign {
  display: inline-block;
  min-width: 120px;
  line-height: 1;
  text-align: center;
  padding: 13px;
  background: #fff;
  color: #1d354e;
  border-radius: 16px;
}

.header__lang {
  position: relative;
  z-index: 2;
  top: 5px;
  margin-right: 10px;
}

.no-touch .header__lang:hover .lang__list {
  opacity: 1;
  visibility: visible;
}

.no-touch .header__lang:hover .lang__current {
  border-radius: 16px 16px 0 0;
}

.header__lang .lang__current {
  min-width: 80px;
  text-align: center;
  display: inline-block;
  line-height: 1;
  padding: 13px;
  background: #fff;
  border-radius: 16px;
  color: #1d354e;
}

.no-touch .header__lang .lang__current:hover {
  text-decoration: underline;
}

.header__lang .lang__list {
  position: absolute;
  width: 100%;
  top: 70%;
  left: 0;
  opacity: 0;
  visibility: hidden;
  padding: 13px;
  background: #fff;
  border-radius: 0 0 16px 16px;
  text-align: center;
  display: inline-block;
}

.no-touch .header__lang .lang__list:hover {
  text-decoration: underline;
}

.header__lang .lang__list .lang_extra {
  color: #1d354e;
}

.button {
  display: inline-block;
  border: none;
  color: #fff;
  text-decoration: none;
  background-color: #1d354e;
  padding: 22px 67px;
  font-size: 18px;
  font-weight: 600;
  font-family: "Montserrat", "Helvetica", sans-serif;
  border-radius: 32px;
  text-align: center;
  outline: none;
  cursor: pointer;
}

.button:focus, .button:hover {
  text-decoration: none;
  color: #fff;
  outline: none;
}

.no-touch .button:hover {
  background-color: #244261;
}

.button:active {
  background-color: #16283b;
}

.robot_container {
  position: relative;
}

.robot__img {
  position: absolute;
  top: -135px;
  left: -40px;
  pointer-events: none;
  user-select: none;
  z-index: -1;
}

.robot__heading {
  max-width: 660px;
  text-align: center;
  margin: 118px 45px 0 auto;
  line-height: 1.2;
  padding: 0 30px;
}

.robot__heading h2 {
  font-size: 72px;
  font-weight: 600;
}

.robot__heading p {
  font-size: 30px;
  font-weight: 600;
  margin: 7px 0 20px 13px;
  line-height: 1.6;
}

.robot__tablet-wrapper {
  width: 100%;
  background: #fff;
  height: 720px;
  border-radius: 16px;
  margin-top: 210px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: -2;
  padding: 0 48px;
}

.robot__tablet-inner {
  max-width: 1074px;
  width: 100%;
  min-height: 624px;
  border-radius: 8px;
  background: #222222;
  display: flex;
  justify-content: center;
  align-items: center;
}

.robot__tablet-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
}

.robot__box1 {
  position: absolute;
  top: 6.2%;
  left: 7%;
  z-index: -2;
}

.robot__box2 {
  position: absolute;
  top: 545px;
  right: 10%;
  z-index: -2;
}

.forecast {
  margin: 140px auto 0;
  position: relative;
}

.forecast__container {
  max-width: 1050px;
}

.forecast__heading {
  font-size: 60px;
  font-weight: 600;
  padding: 0 20px 2px;
}

.forecast__desc {
  font-size: 24px;
  font-weight: 600;
  padding: 0 20px;
}

.forecast__currency {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 40px;
  margin-left: 13px;
}

.forecast__currency li {
  width: 136px;
  height: 84px;
}

.forecast__box1 {
  position: absolute;
  top: -65px;
  right: 7.5%;
  z-index: -1;
}

.forecast__box2 {
  position: absolute;
  top: 60px;
  left: 0;
  z-index: -1;
}

.currency__item {
  display: block;
  text-align: center;
  text-transform: uppercase;
  border: 8px solid #223e5c;
  background: transparent;
  max-width: 136px;
  width: 100%;
  max-height: 84px;
  height: 100%;
  border-radius: 8px;
  font-size: 30px;
  font-weight: 700;
  color: #fff;
  transition: .3s;
}

.no-touch .currency__item:hover {
  background: #00bdca;
  border: 8px solid #00bdca;
}

.graph {
  margin: 40px auto 0;
  position: relative;
}

.graph__img {
  display: block;
  margin: 0 auto;
  position: relative;
  z-index: -2;
  width: 100%;
  padding: 0 30px;
}

.indicators {
  display: flex;
  position: absolute;
  left: 27.4%;
  top: 5.5%;
  line-height: 1.3;
}

.indicators__item {
  margin-right: 45px;
}

.indicator__main {
  text-transform: uppercase;
  font-size: 24px;
  font-weight: 700;
}

.indicator__main_centered {
  line-height: 2.6;
}

.indicator__aside {
  font-size: 12px;
  font-weight: 500;
  text-align: right;
  margin-left: -15px;
}

.indicator__aside span {
  color: #71d44e;
}

.indicator__aside_arrow {
  margin-bottom: 2px;
}

article {
  margin-top: 142px;
}

.article {
  background: #fff;
  color: #1d354e;
  border-radius: 16px;
  overflow: hidden;
}

.article__heading {
  font-size: 60px;
  font-weight: 600;
  padding: 80px 100px 0;
}

.post {
  display: flex;
  max-width: 1100px;
  margin: 0 auto;
  flex-direction: column;
  flex-wrap: wrap;
  max-height: 1120px;
  padding: 35px;
  align-items: flex-end;
}

.post__item {
  display: flex;
  max-width: 460px;
  word-wrap: break-word;
  width: 50%;
  margin: 0 18px 45px 0;
}

.post__item svg {
  margin-right: 27px;
}

.post__item p {
  font-size: 18px;
  font-weight: 300;
  max-width: 80%;
  margin-top: -10px;
  line-height: 2;
}

.news {
  margin-top: 110px;
}

.news__container {
  max-width: 1032px;
  position: relative;
}

.news__box {
  position: absolute;
  right: -38%;
  top: 15%;
  pointer-events: none;
}

.news__heading {
  font-size: 60px;
  font-weight: 600;
}

.news__button {
  margin: 30px auto;
  padding: 22px 30px;
  max-width: 320px;
  display: block;
}

.news-inner {
  display: flex;
  margin-top: 26px;
}

.news-inner__item {
  margin-right: 36px;
  max-width: 300px;
  width: 100%;
}

.news-inner__item:last-child {
  margin-right: 0;
}

.news-inner__img {
  max-width: 300px;
  width: 100%;
  height: 300px;
  border-radius: 8px;
  background: #222;
}

.news-inner__heading {
  font-size: 30px;
  font-weight: 600;
  margin: 15px 0;
  line-height: 1.25;
}

.news-inner__descr {
  font-size: 14px;
  font-weight: 400;
  line-height: 1.6;
}

.faq {
  position: relative;
  background: #fff;
  color: #1d354e;
  border-radius: 16px;
  max-width: 1170px;
  width: 90%;
  margin: 112px auto;
  min-height: 900px;
}

.faq__img {
  position: absolute;
  top: 10%;
  left: -7.7%;
}

.faq__container {
  max-width: 1082px;
}

.faq__heading {
  font-size: 72px;
  font-weight: 600;
  padding: 80px 30px 55px;
  margin: 0 auto;
  line-height: 1;
}

.faq__heading span {
  font-weight: 200;
}

.accordion {
  margin-left: 30px;
}

.accordion__item {
  display: flex;
  cursor: pointer;
  padding-bottom: 27px;
  flex-wrap: wrap;
  line-height: 1.5;
}

.accordion__circle {
  height: 36px;
  width: 36px;
  border-radius: 50%;
  background: #00bdca;
  flex: none;
  position: relative;
  margin-right: 16px;
  margin-top: 5px;
}

.accordion__arrow {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.accordion__arrow_active {
  transform: rotate(-90deg);
}

.accordion__question {
  font-size: 30px;
  font-weight: 600;
  max-width: 92%;
}

.accordion__answer {
  width: 100%;
  padding-top: 12px;
  padding-left: 50px;
  display: none;
  background-color: white;
  overflow: hidden;
  font-size: 18px;
  font-weight: 300;
  cursor: text;
  line-height: 2;
}

.footer {
  padding-top: 60px;
  min-height: 660px;
  overflow: hidden;
}

.footer__container {
  max-width: 1030px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  position: relative;
}

.footer__bg {
  background: #1d354e;
  position: absolute;
  width: 200%;
  height: 140%;
  transform: rotate(30deg);
  z-index: -2;
  top: 52%;
  left: -120%;
  pointer-events: none;
}

.footer__telegram {
  padding: 70px 0;
}

.footer__baseline {
  width: 100%;
}

.footer__box1 {
  position: absolute;
  z-index: -1;
  pointer-events: none;
  right: -28%;
  top: -12%;
}

.footer__box2 {
  position: absolute;
  z-index: -1;
  pointer-events: none;
  left: -32.5%;
  top: 18%;
}

.telegram {
  text-align: center;
}

.telegram__links-wrapper {
  padding-bottom: 10px;
}

.telegram__link {
  margin-bottom: 20px;
  min-width: 370px;
  padding: 20px;
  padding-right: 47px;
  color: #1d354e;
  background: #fff;
  transition: 300ms;
}

.telegram__link:focus {
  color: #1d354e;
}

.no-touch .telegram__link:hover {
  background-color: #244261;
  color: #fff;
}

.no-touch .telegram__link:hover .telegram__icon path {
  fill: #fff;
}

.telegram__link:active {
  background-color: #16283b;
}

.telegram__icon {
  position: relative;
  top: 5px;
  right: 5px;
}

.telegram__icon path {
  transition: 300ms;
}

.telegram__mail {
  font-size: 18px;
  font-weight: 600;
}

.form {
  max-width: 500px;
  min-height: 441px;
  border: 16px solid #56ecf6;
  border-radius: 16px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form__heading {
  font-size: 60px;
  font-weight: 200;
  padding: 44px 50px 16px;
  line-height: 1;
}

.form__heading span {
  font-weight: 600;
}

.form__input {
  width: 100%;
  max-width: 370px;
  background: transparent;
  border-bottom: 2px solid #56ecf6;
  padding: 20px;
  padding-left: 15px;
  font-size: 24px;
  font-weight: 600;
  color: #fff;
}

.form__input::placeholder {
  color: #fff;
}

.form__button {
  margin-top: 40px;
  min-width: 370px;
}

.baseline {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 50px;
}

.socials {
  display: flex;
}

.socials__item {
  display: inline-block;
  min-height: 36px;
  min-width: 36px;
  border: 4px solid #fff;
  border-radius: 10px;
  position: relative;
  margin-right: 12px;
}

.socials__item .facebook {
  position: absolute;
  top: 27%;
  left: 48%;
}

.socials__item .insta {
  position: absolute;
  top: 14%;
  left: 22%;
}

.socials__item .youtube, .socials__item .vk {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.aside {
  display: flex;
  min-width: 240px;
  justify-content: space-between;
  font-size: 18px;
  font-weight: 600;
}
