.news {
	margin-top: 110px;
	&__container {
		max-width: 1032px;
		position: relative; }
	&__box {
		position: absolute;
		right: -38%;
		top: 15%;
		pointer-events: none; }
	&__heading {
		@include f(60px, 600); }
	&__button {
		margin: 30px auto;
		padding: 22px 30px;
		max-width: 320px;
		display: block; } }

.news-inner {
	display: flex;
	// flex-wrap: wrap
	margin-top: 26px;
	&__item {
		margin-right: 36px;
		max-width: 300px;
		width: 100%;
		&:last-child {
			margin-right: 0; } }
	&__img {
		max-width: 300px;
		width: 100%;
		height: 300px;
		border-radius: 8px;
		background: #222; }
	&__heading {
		@include f(30px, 600);
		margin: 15px 0;
		line-height: 1.25; }
	&__descr {
		@include f(14px, 400);
		line-height: 1.6; } }

