.robot {
	&_container {
		position: relative; }
	&__img {
		position: absolute;
		top: -135px;
		left: -40px;
		pointer-events: none;
		@include nosel;
		z-index: -1; }
	&__heading {
		max-width: 660px;
		text-align: center;
		margin: 118px 45px 0 auto;
		line-height: 1.2;
		padding: 0 30px;
		& h2 {
			@include f(72px, 600); }
		& p {
			@include f(30px, 600);
			margin: 7px 0 20px 13px;
			line-height: 1.6; } }

	&__tablet-wrapper {
		width: 100%;
		background: #fff;
		height: 720px;
		border-radius: 16px;
		margin-top: 210px;
		display: flex;
		justify-content: center;
		align-items: center;
		position: relative;
		z-index: -2;
		padding: 0 48px; }
	&__tablet-inner {
		max-width: 1074px;
		width: 100%;
		min-height: 624px;
		border-radius: 8px;
		background: $darkgray;
		display: flex;
		justify-content: center;
		align-items: center; }
	&__tablet-icon {
		@include vcenter;
		z-index: 1; }
	&__box1 {
		position: absolute;
		top: 6.2%;
		left: 7%;
		z-index: -2; }
	&__box2 {
		position: absolute;
		top: 545px;
		right: 10%;
		z-index: -2; } }
