.header {
	&__container {
		display: flex;
		padding: 60px 0 0;
		max-width: 1060px; }
	&__logo {
		display: flex;
		align-items: center;
		margin-left: 55px;
		& img {
			width: 44px;
			height: 44px;
			margin-right: 12px; }
		& .title {
			line-height: 1;
			& h1 {
				@include f(24px, 600);
				& span {
					color: #56ecf6; } }
			& p {}
			@include f(13px, 500); } }
	&__menu {
		margin-left: auto; } }

.nav {
	display: flex;
	align-items: center;
	// height: 50px
	line-height: 3.3; }
.navigation__item {
	margin-right: 34px; }

.header__login {
	margin-left: 5px;
	margin-right: 8px;
	& .login_log {
		margin-right: 30px; }
	& .login_sign {
		display: inline-block;
		min-width: 120px;
		// max-height: 50px
		line-height: 1;
		text-align: center;
		padding: 13px;
		background: #fff;
		color: $darkblue;
		border-radius: 16px; } }

.header__lang {
	position: relative;
	z-index: 2;
	top: 5px;
	margin-right: 10px;
	@include hover {
		.lang__list {
			opacity: 1;
			visibility: visible; }
		.lang__current {
			border-radius: 16px 16px 0 0; } }
	& .lang__current {
		min-width: 80px;
		text-align: center;
		display: inline-block;
		// max-height: 42px
		line-height: 1;
		padding: 13px;
		background: #fff;
		border-radius: 16px;
		color: $darkblue;
		@include hover {
			text-decoration: underline; } }
	& .lang__list {
		position: absolute;
		width: 100%;
		top: 70%;
		left: 0;
		opacity: 0;
		visibility: hidden;
		padding: 13px;
		background: #fff;
		border-radius: 0 0 16px 16px;
		text-align: center;
		display: inline-block;
		@include hover {
			text-decoration: underline; }
		& .lang_extra {
			color: $darkblue; } } }
